const app = getApp();
import helper from '../common/helper.js';
export default {
	data() {
		return {
			navList: [
				{ id: '1', img: 'iconxiepinglun', title: '实战心得' },
				{ id: '2', img: 'iconliebiao', title: '拒贷汇总' },
				{ id: '3', img: 'iconbulletin', title: '微金公告' },
				{ id: '4', img: 'iconyonghu', title: '从业感悟' },
				{ id: '5', img: 'iconqiapiansousuo', title: '论坛搜索' }
			],
			Inv: 0,
			pageData: '',
			imgUrl:'',
			page: '1'
		};
	},
	onLoad() {
		// 加载微金交流首页数据
		this.getIndexData();
		this.imgUrl = helper.imgUrl
	},
	methods: {
		selListType(e) {
			this.Inv = e.currentTarget.dataset.index
			this.boardId = e.currentTarget.dataset.block_id
		},
		goDetail(e){
			uni.navigateTo({
				url:`/pages/articleDetail?id=${e}`
			})
		},
		// 数据
		getIndexData() {
			uni.showLoading({
				title: '加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/forum/index`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == '200') {
						this.pageData = res.data.data;
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		},
		// 导航子页面跳转
		getNav(e) {
			console.log(e);
			let id = e;
			if (id == '1') {
				uni.navigateTo({
					url: `/pages/experience`
				});
			}
		},
		// 加载下一页
		onReachBottom(){
			this.page ++;
			this.getList()
		},
		getList() {
			uni.showLoading({
				title: '加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/forum/posts`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				data:{
					board_id: this.boardId,
					page: this.page,
					page_size: '10'
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == '200') {
						this.pageData = this.pageData.concat(res.data.data);
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		},
	}
};
